* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: #061922;
    color: whitesmoke;
}

form {
    padding-top: 2rem;
}

input {
    margin-top: 2rem;
    height: 2rem;
    margin-bottom: 1rem;
}

label {
    font-size: 3rem;
}

#item {
    width: 100vw;
}

.btn {
    width: 100vw;
    outline: none;
    border: none;
    font-weight: 800;
    background-color: blue;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    cursor: pointer;
}

.form-row {
    text-align: center;
}

#add-btn {
    margin-bottom: 2rem;
}